export default function BuyingPage() {
  const [selectedTariff, setSelectedTariff] = useState<string | null>(null)
  const [billingPeriod, setBillingPeriod] = useState<BillingPeriod>("month")
  const [serverName, setServerName] = useState("")
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)
  const { t } = useLanguage()
  const { formatPrice, convertPrice } = useCurrency()

  const handleBuy = async () => {
    if (!selectedTariff || !serverName) return

    setLoading(true)
    setSuccess(false)
    const tariff = tariffs.find(t => t.id === selectedTariff)
    try {
      // –ó–¥–µ—Å—å –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å —Ä–µ–∞–ª—å–Ω—ã–π –ø—Ä–æ—Ü–µ—Å—Å –∑–∞–∫–∞–∑–∞ —Å–µ—Ä–≤–µ—Ä–∞ —á–µ—Ä–µ–∑ –≤–∞—à API
      // –ü—Ä–∏–º–µ—Ä –∑–∞–ø—Ä–æ—Å–∞:
      await fetch("/api/orders", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          serverName,
          serverPlan: tariff?.name,
          billingPeriod,
          price: tariff?.prices[billingPeriod],
        }),
      })

      setSuccess(true)
      setTimeout(() => setSuccess(false), 4000)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="max-w-5xl mx-auto py-8">
     <DashboardHeader />
      <h1 className="text-3xl font-bold text-foreground mb-6">{t("servers.buyTitle") || "–ö—É–ø–∏—Ç—å —Å–µ—Ä–≤–µ—Ä"}</h1>
      <div className="grid md:grid-cols-2 gap-8">
        {/* Tariff List */}
        <div>
          <h2 className="text-xl font-semibold mb-3">{t("servers.choosePlan") || "–í—ã–±–µ—Ä–∏—Ç–µ —Ç–∞—Ä–∏—Ñ"}</h2>
          <div className="space-y-3 max-h-[650px] overflow-y-auto pr-2">
            {tariffs.map((tariff) => (
              <div
                key={tariff.id}
                className={`transition-colors cursor-pointer border rounded-lg p-4 bg-background/70 flex items-center gap-4 ${
                  selectedTariff === tariff.id ? "border-primary shadow-lg" : "border-border/40 hover:border-primary/60"
                }`}
                onClick={() => setSelectedTariff(tariff.id)}
              >
                <div className="w-10 h-10 rounded-md flex items-center justify-center bg-primary/10">
                  <Server className="w-5 h-5 text-primary" />
                </div>
                <div className="flex-1">
                  <div className="font-semibold text-foreground">{tariff.name}</div>
                  <div className="text-sm text-muted-foreground">
                    {tariff.cores} CPU &middot; {tariff.ram} GB {tariff.ramType} &middot; {tariff.storage} GB {tariff.ramType === "DDR5" ? "" : "NVMe"}
                  </div>
                </div>
                <div className="flex flex-col gap-1 min-w-[120px]">
                  <div className="text-lg font-bold text-primary">
                    ${tariff.prices.month} <span className="text-xs text-muted-foreground">/–º–µ—Å</span>
                  </div>
                  <div className="text-base font-semibold text-foreground">
                    ${tariff.prices.hour} <span className="text-xs text-muted-foreground">/—á–∞—Å</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {/* Order Panel */}
        <div className="space-y-6 max-w-md mx-auto w-full">
          <h2 className="text-xl font-semibold">{t("servers.orderConfig") || "–ù–∞—Å—Ç—Ä–æ–π–∫–∏ –∑–∞–∫–∞–∑–∞"}</h2>
          <div className="flex flex-col gap-4">
            <div>
              <label className="block mb-1 font-medium">{t("servers.nameLabel") || "–ò–º—è —Å–µ—Ä–≤–µ—Ä–∞"}</label>
              <Input
                placeholder={t("servers.namePlaceholder") || "–ù–∞–ø—Ä–∏–º–µ—Ä, my-server"}
                value={serverName}
                onChange={e => setServerName(e.target.value)}
                disabled={loading}
              />
            </div>
            <div>
              <label className="block mb-1 font-medium">{t("servers.billing") || "–ü–ª–∞—Ç–µ–∂–Ω—ã–π –ø–µ—Ä–∏–æ–¥"}</label>
              <div className="flex gap-3">
                <Button
                  variant={billingPeriod === "month" ? "default" : "outline"}
                  onClick={() => setBillingPeriod("month")}
                  size="sm"
                  type="button"
                  disabled={loading}
                >
                  {t("servers.monthly") || "–ú–µ—Å—è—Ü"}
                </Button>
                <Button
                  variant={billingPeriod === "hour" ? "default" : "outline"}
                  onClick={() => setBillingPeriod("hour")}
                  size="sm"
                  type="button"
                  disabled={loading}
                >
                  {t("servers.hourly") || "–ß–∞—Å"}
                </Button>
              </div>
            </div>
            {selectedTariff && (
              <div>
                <p className="font-semibold mb-1">{t("servers.selectedPlan") || "–í—ã–±—Ä–∞–Ω–Ω—ã–π —Ç–∞—Ä–∏—Ñ:"}</p>
                <div className="flex items-center gap-2">
                  <Badge>{tariffs.find(t => t.id === selectedTariff)?.name}</Badge>
                  <span className="ml-4">
                    {billingPeriod === "month"
                      ? `$${tariffs.find(t => t.id === selectedTariff)?.prices.month}/–º–µ—Å`
                      : `$${tariffs.find(t => t.id === selectedTariff)?.prices.hour}/—á–∞—Å`}
                  </span>
                </div>
              </div>
            )}
            <Button
              className="w-full mt-3"
              onClick={handleBuy}
              disabled={!selectedTariff || !serverName || loading}
            >
              {loading
                ? t("servers.buying") || "–ü–æ–∫—É–ø–∫–∞..."
                : t("servers.buyButton") || "–ö—É–ø–∏—Ç—å"}
            </Button>
            {success && (
              <div className="text-green-600 font-medium mt-2">
                {t("servers.successOrder") || "–°–µ—Ä–≤–µ—Ä —É—Å–ø–µ—à–Ω–æ –∑–∞–∫–∞–∑–∞–Ω!"}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
